.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App {
  height: 100vh;
  padding-top: 60px;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  color: #000;
  -webkit-font-smoothing: antialiased;
  font-family: 'Overpass', Arial, Helvetica, sans-serif;
  line-height: 1.6;
}

nav {
  margin: 0 auto;
  text-align: center;
}

nav ul li a {
  text-decoration: none;
  color: #e4c465;
  font-weight: 900;
  font-size: 12px;
  letter-spacing: 2px;
  text-transform: uppercase;
  line-height: 0.5;
  position: relative;
}

nav ul li a:before {
  bottom: -10px;
  content: '';
  position: absolute;
  width: 0%;
  height: 2px;
  background-color: #e4c465;
  transition: all 0.2s;
}

nav ul li a:hover:before {
  width: 100%;
}

.logo {
  width: 100px;
}

.logo-on-nav {
  width: 35% !important;
}

.full-size {
  width: 100%;
  position: relative;
}

span.blue,
span.red {
  background-color: white;
  padding: 4px 10px;
  border-radius: 2px;
  font-weight: 900;
}

span.blue {
  color: #2322a6;
}

span.red {
  color: #9d151e;
}

img {
  max-width: 100%;
}

li {
  list-style-type: none;
  padding: 0;
}

.flex-parent {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: 0;
  justify-content: center;
  align-items: center;
}

.flex-parent-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.flex-parent > li {
  width: 18%;
}

.d-inline-block {
  display: inline-block;
}

.nav-slider {
  position: absolute;
  bottom: 0;
  right: 0;
}

.card {
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.03), 0 1px 2px rgba(0, 0, 0, 0.3),
    0 8px 12px rgba(0, 0, 0, 0.1);
}

.prev-button i,
.next-button i {
  font-size: 40px;
}

.prev-button,
.next-button {
  padding: 20px 30px;
  margin-right: 1px;
  background: rgba(0, 0, 0, 0.5);
  cursor: pointer;
  transition: all 0.3s;
}

.prev-button:hover,
.next-button:hover {
  opacity: 0.9;
}

.homepage {
  height: calc(100% - 57px);
  color: #fff;
}

.homepage-background {
  background-image: url('./assets/images/picture1.jpg');
  background-size: cover;
  background-position: center;
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  z-index: -2;
  opacity: 1;
}

.homepage-background-overlay {
  background-image: url('./assets/images/background.jpg');
  background-size: cover;
  background-position: center;
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  z-index: -1;
  opacity: 0.98;
}

.other-than-homepage .homepage-background,
.other-than-homepage .homepage-background-overlay {
  height: 500px;
}

.content {
  width: 1000px;
  margin: 0 auto;
  position: relative;
}

.content.header-wrapper {
  height: 383px;
}

.huge-company-name {
  font-size: 122px;
  line-height: 1;
  font-weight: 900;
  letter-spacing: -5px;
  position: relative;
}

.huge-company-name.header {
  color: #fff;
  font-size: 80px;
}

.huge-company-name:before {
  position: absolute;
  content: '';
  left: 0px;
  top: -20px;
  height: calc(100% + 90px);
  width: 1px;
  background-color: rgba(255, 255, 255, 0.2);
}

.huge-company-name:after {
  position: absolute;
  content: '';
  bottom: 0px;
  left: -100px;
  height: 1px;
  width: calc(100% + 100px);
  background-color: rgba(255, 255, 255, 0.2);
}

.brief-description-company {
  margin-top: 30px;
  font-size: 20px;
  padding-left: 30px;
  font-weight: 300;
}

.about-the-business {
  padding: 50px 0;
}

.about-the-business-bg {
  background-color: blue;
  position: absolute;
  top: 0;
  right: 0;
  height: 0;
  width: 20%;
}

.about-the-business .title,
.visi-misi .title {
  width: 30%;
  font-size: 50px;
  position: relative;
  padding-top: 30px;
  line-height: 1.2;
  font-weight: 900;
  letter-spacing: -3px;
}

.about-the-business .title:before,
.visi-misi .title:before {
  position: absolute;
  top: 0;
  left: 0;
  content: '';
  height: 5px;
  width: 50%;
  background-color: #9d151e;
}

.about-the-business .title:after,
.visi-misi .title:after {
  position: absolute;
  top: 0px;
  right: 0;
  content: '';
  height: 5px;
  width: 50%;
  background-color: #2322a6;
}

.about-the-business .text,
.visi-misi .text {
  width: 70%;
  padding: 100px;
  text-align: justify;
  padding-left: 0;
}

.p-text {
  font-size: 23px;
  font-weight: 300;
  font-family: 'Chivo', Arial, Helvetica, sans-serif;
}

.visi-misi .title,
.visi-misi .text {
  width: unset;
  padding-left: 0;
  padding-right: 100px;
  padding-top: 30px;
  padding-bottom: 0;
  display: inline-block;
}

.visi-misi {
  padding: 0px 0 150px;
}

.visi-misi .title {
  padding-right: 0 !important;
}

.visi-misi .picture {
  width: 50%;
}

.visi-misi .picture img {
  width: 100%;
}

.title-and-text {
  padding-left: 0px;
  width: 50%;
}

.about-the-business .picture {
  width: 30%;
}
